import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  const dispatch = useDispatch();
  const [isDisplayProgress, setIsDisplayProgress] = React.useState(false);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    { dataField: "areaName", text: "Area", type: "string", sort: true },
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    },
    {
      dataField: "ripenessLevel",
      text: "Ripeness",
      type: "string",
      sort: true,
    },
    { dataField: "qty", text: "Input Weight (KG)", type: "number", sort: true },
    { dataField: "inputPack", text: "Input Pack", type: "number", sort: true },
    {
      dataField: "newGrade",
      text: "Grade",
      type: "string",
      sort: true,
    },
  ];

  const dataSet1 = [
    {
      data: "",
    },
  ];

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="Repacking_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="Area" value="data" />
          <ExcelColumn label="PO SKU Code" value="data" />
          <ExcelColumn label="SKU Number" value="data" />
          <ExcelColumn label="Expiry Date" value="data" />
          <ExcelColumn label="Ripeness" value="data" />
          <ExcelColumn label="Input Weight (KG)" value="data" />
          <ExcelColumn label="Input Pack" value="data" />
          <ExcelColumn label="Grade" value="data" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push("/inventory-entry/repacking/CSV-upload/review")
        }
        transaction={"repacking"}
        field={Field}
        type="repacking"
      />
    </div>
  );
};

export default withRouter(Upload);
